// Grid
.property-item {
	margin-bottom: 30px;
}

.property-image {
	position: relative;

	.property-status {
		display: flex;
		position: absolute;
		right: 8px;
		top: 8px;
		margin-bottom: 0px;

		li {
			font-size: 13px;
			color: #fff;
			padding: 3px 8px;
			margin-right: 6px;

			&:last-child {
				margin-right: 0px;
			}

		}

		li.property-feature {
			background: $primary;
		}

		li.property-rent {
			background: $gray-8;
		}

	}

	.property-lable {
		display: flex;
		position: absolute;
		right: 8px;
		top: 8px;
	}

	.property-trending {
		&:before {
			content: "";
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 55px 55px 0 0;
			border-color: #ffc107 transparent transparent transparent;
			position: absolute;
			left: 0px;
			top: 0px;
		}

		.fas {
			position: absolute;
			top: 12px;
			left: 12px;
			color: #fff;
		}

	}

}

.property-details {
	background: $white;
	border: 1px solid $border-color;
	border-top: none;

	i {
		margin-right: 8px;
	}

	.property-info {
		margin-bottom: 0px;
		border-bottom: 1px solid $border-color;

		li {
			padding: 15px 10px;
			font-size: 13px;
			text-align: center;
			border-right: 1px solid $border-color;

			&:last-child {
				border-right: none;
			}

			span {
				color: $gray-8;
				font-weight: bold;
				margin-right: 3px;
			}

		}

	}

	.property-details-inner {
		padding: 20px;

		.property-title {
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}

	}

	.property-agent {
		display: flex;
		align-items: center;
		font-size: 13px;
		padding-top: 25px;

		.property-agent-image {
			width: 22px;
			margin-right: 10px;

			img {
				width: 100%;
				border-radius: 100%;
			}

		}

		.property-agent-name {
			color: $gray-3;

			&:hover {
				color: $primary;
			}

		}

		.property-agent-date {
			margin-left: auto;
		}

	}

	.property-listing-actions {
		display: flex;
		border-top: 1px solid $border-color;
		border-bottom: 1px solid $border-color;

		li {
			display: flex;
			position: relative;

			&:before {
				content: "";
				border-right: 1px solid $border-color;
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
			}

			&:first-child {
				&:before {
					content: none;
				}

			}

			a {
				align-self: center;
				color: $gray-3;
				padding: 12px 20px;

				&:hover {
					color: $primary;
				}

			}

			i {
				margin-right: 0px;
			}

		}

		li.property-price {
			margin-right: auto;
			color: $primary;
			font-size: 18px;
			padding: 12px 20px;
		}

	}

	.property-btn {
		a {
			display: block;
			padding: 15px;
			font-size: 15px;
			text-align: center;
			background: $gray-1;
			color: $gray-8;

			&:hover {
				background: $primary;
				color: $white;
			}

		}

	}

}

// List
.property-col-list {
	.property-details {
		display: flex;
		flex-direction: column;
		height: 100%;
		border-top: 1px solid $border-color;
		border-left: none;

		.property-agent {
			padding: 0px 20px 20px;
			margin-top: auto;
		}

	}

}

// Property Offter
.property-offer {
	.property-offer-image {
		padding: 90px 80px;
	}

	.property-details {
		.property-agent {
			padding: 0px 20px 20px;
		}

	}

}

.property-map {
	.map-canvas {
		height: 500px;
	}

}

// Property Details Page
.property-list {
	li {
		b {
			color: $gray-8;
			font-weight: 600;
			margin-right: 3px;
		}

	}

}

.property-list-style-2 {
	li {
		display: flex;
		align-items: center;
		line-height: 30px;

		&:before {
			content: "\f111";
			font-family: "Font Awesome 5 Free";
			font-style: normal;
			font-weight: 900;
			color: $primary;
			font-size: 5px;
			margin-right: 10px;
		}

	}

}

.walk-score-info {
	align-items: center;
}

.property-nearby {
	.nearby-title {
		font-size: 16px;
	}

}

.property-search-field {
	.property-search-item {
		position: relative;
		border: 1px solid $border-color;
		border-bottom: 0;
	}

	.form-row {
		margin: 0px;
	}

	.form-row.property-price-slider {
		border-top: 1px solid $border-color;
	}

	.form-group {
		padding: 24px 28px;
		margin-bottom: 0px;
		border-left: 1px solid $border-color;
		border-bottom: 1px solid $border-color;

		&:first-child {
			border-left: none;
		}

		label {
			display: block;
		}

	}

	.form-control {
		background: transparent;
		color: $gray-8;
		padding: 0px;
		height: auto;
		line-height: normal;
		border: none;

		&::placeholder {
			color: $gray-8;
		}

	}

	i {
		padding-right: 5px;
	}

	.more-search {
		color: $gray-3;
		display: block;

		&:hover {
			color: $primary;
		}

	}

	.form-group-search {
		width: 100%;

		i {
			color: $primary;
		}

	}

	.advanced-search {
		position: absolute;
		left: 0px;
		right: 0px;
		top: 100%;
		z-index: 99;
		margin: -1px 0px;
		width: 100%;
	}

	.card {
		padding: 0px;
		border-radius: 0px;
		border-color: $border-color;
	}

}

// Property Filter
.property-filter {
	border: 1px solid $border-color;

	ul {
		li {
			padding: 20px;
			border-right: 1px solid $border-color;

			a {
				color: $body-color;

				&:hover {
					color: $primary;
				}

			}

		}

		li.active {
			a {
				color: $primary;
			}

		}

		&:nth-child(2n+2) {
			li {
				&:last-child {
					border-right: none;
				}

			}

		}

	}

	.property-short {
		margin-left: auto;
		margin-left: auto;

		.select2-container {
			min-width: 130px;
			margin-left: 10px;
		}

	}

}

// property-detail
.property-detail-meta {
	display: flex;
	margin: 20px 0 30px;

	> li {
		> a {
			margin-right: -1px;
			border: 1px solid $border-color;
			padding: 14px 20px;
			display: block;
			color: $gray-3;

			&:hover {
				color: $primary;
			}

		}

	}

}

.share-box {
	position: relative;

	.share-box-social {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		left: 50%;
		bottom: 100%;
		display: flex;
		background: $primary;
		padding: 10px;
		transition: all 0.3s ease-in-out;
		border-radius: $border-radius;
		transform: translate(-50%, 0px);

		li {
			a {
				padding: 0 10px;
				color: $white;
				font-size: 13px;
				display: block;

				&:hover {
					color: $gray-8;
				}

			}

		}

		&:before {
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 6px 6px 0 6px;
			border-color: $primary transparent transparent transparent;
			content: "";
			position: absolute;
			left: 50%;
			bottom: -6px;
			z-index: 99;
			margin-left: -3px;
		}

	}

	&:hover {
		.share-box-social {
			opacity: 1;
			visibility: visible;
			margin-bottom: 6px;
		}

	}

}

.property-detail-gallery {
	position: relative;

	.nav-tabs.nav-tabs-02 {
		position: absolute;
		z-index: 9;
		top: 20px;
		left: 20px;

		.nav-item {
			margin-right: 4px;
		}

	}

}

.property-detail-gallery .slider-slick,
.property-detail-gallery iframe,
.property-detail-gallery #street-view {
	min-height: 500px;
}

// property item map
.si-content-wrapper {
	padding: 0;
}

.custom-window.open {
	.si-frame {
		box-shadow: none;
		overflow: visible;
	}

}

.map-canvas {
	width: 100%;
	height: 100%;
}

.custom-window {
	top: 30px;
	width: 280px;
	-webkit-transition: top 0.3s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1), visibility 0.3s cubic-bezier(0.4, 0, 0.2, 1);
	transition: top 0.3s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1), visibility 0.3s cubic-bezier(0.4, 0, 0.2, 1);
	opacity: 0;
	visibility: hidden;

	.si-content {
		overflow: visible;
	}

}

.custom-window.active {
	top: 0;
	opacity: 1;
	visibility: visible;
}

.property-item-map {
	position: relative;
	max-height: inherit;
	padding: 180px 12px 12px 12px;
	box-shadow: 0 4px 16px rgba(black, 0.1);
	overflow: auto;
	z-index: 2;
}

.si-pointer-bg-top {
	box-shadow: 0 4px 16px rgba(black, 0.1);
}

.property-item-map-img {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	height: 180px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	z-index: 1;
}

.property-item-map-title {
	margin-top: 12px;
}

.property-item-map-price {
	margin-top: 8px;
	display: flex;
	align-items: center;
}

.property-item-map-content {
	p {
		margin: 0;
	}

	* {
		+ {
			p {
				margin-top: 1em;
			}

		}

	}

	a {
		color: $gray-3;

		&:hover {
			color: $primary;
		}

		&:focus {
			color: $primary;
		}

		&:active {
			color: $primary;
		}

	}

}

.custom-close {
	position: absolute;
	top: -8px;
	right: -8px;
	z-index: 9;
	width: 24px;
	padding: 0 8px;
	height: 24px;
	border-radius: 5px;
	-webkit-transition: background-color 0.15s cubic-bezier(0.4, 0, 0.2, 1);
	transition: background-color 0.15s cubic-bezier(0.4, 0, 0.2, 1);
	border: 0;
	background-color: $primary;
	color: $white;
	font-size: 20px;
	line-height: 20px;
	cursor: pointer;
	transition: all 0.4s ease-in-out;

	&:hover {
		background-color: $gray-8;
	}

	&:focus {
		background-color: $gray-8;
	}

	&:active {
		background-color: $gray-8;
	}

}

// Property Search
.property-search-field-top {
	margin-top: -100px;
	position: relative;
	z-index: 99;
}

.property-search-field-top-02 {
	margin-top: -50px;
	position: relative;
	z-index: 99;
}

.property-search-field.property-search-field-02.property-search-field-top-03 {
	margin-top: -56px;
	position: relative;
	z-index: 99;

	.property-search-item {
		background: $gray-1;
		padding: 15px;

		.card {
			background: $gray-1;
		}

		.form-row {
			align-items: flex-end;

			.btn {
				margin-top: 0px;
			}

		}

	}

	.form-control {
		background: $white;
	}

}

// property search field below
.property-search-field.property-search-field-below {
	.advanced-search {
		position: relative;
		width: 100%;
		margin: -1px 0px;
	}

	.card {
		border-left: 0;
		border-right: 0;
		border-bottom: 0;
	}

}

// property search field advanced
.property-search-field.property-search-field-advanced {
	.advanced-search {
		position: relative;
		width: 100%;
		margin: -1px 0px;
	}

	.card {
		border-left: 0;
		border-right: 0;
		border-bottom: 0;
	}

}

// property search field 02
.property-search-field.property-search-field-02 {
	.property-search-item {
		border: none;

		.form-row {
			align-items: flex-end;

			.btn {
				margin-top: 30px;
			}

		}

	}

	.advanced-search {
		position: relative;
		width: 100%;
		margin: -1px 0px;
	}

	.card {
		border: none;
	}

	.form-group {
		border: none;
		padding: 10px 10px;
		align-items: center;
	}

	.form-control {
		background: transparent;
		color: $gray-8;
		padding: 14px 20px;
		height: auto;
		line-height: normal;
		border: 1px solid $border-color;

		&::placeholder {
			color: $gray-8;
		}

	}

	.select2-container--default {
		.select2-selection--single {
			border: 1px solid $border-color;
			height: 50px;
			padding: 15px 20px;
			border-radius: 3px;

			.select2-selection__arrow {
				top: 16px;
				right: 20px;
			}

		}

	}

}

.select2-search--dropdown .select2-search__field {
	padding: 8px 14px;
}
